.login {
    // border: 1px solid red;
    max-width: 360px;
    margin: auto;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;
    margin-top: 100px;

    img {
        width: 177px;
        height: 99px;
        margin-bottom: 74px;

        // width: 50%;
        // border: 1px solid $clr1;
        
        // padding: 20px;
        // border-radius: 100px;

        // width: 60%;
    }

    .login-title {
        // border: 1px solid green;
        color: $mh-yellow;
        font-family: 'Open Sans Bold';
        font-weight: 700;
        font-size: 48px;
        line-height: 50px;
        text-align: center;
        margin: 0 17px 42px 17px ;
        // display: flex;
        // align-items: center;
        // text-align: center;
    }

    form {
        display: flex;
        flex-direction: column;
        // justify-content: center;
        align-items: flex-end;
        gap: 10px;

        input {
            border: 1px solid $mh-gray;
            border-radius: 30px;
            background: $mh-white;
            padding: 5px 20px 5px 5px;
            width: 280px;
            height: 40px;
            /* Inside auto layout */
            // flex: none;
            // order: 0;
            // flex-grow: 0;

            // width: 47px;
            // height: 50px;

            font-family: 'Open Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 17px;
            line-height: 50px;
            /* identical to box height, or 294% */

            // display: flex;
            // align-items: center;
            // text-align: center;

            color: $mh-gray;

            /* Inside auto layout */

            // flex: none;
            // order: 0;
            // flex-grow: 0;
        }

        button {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            padding: 5px;
            gap: 10px;

            width: 280px;
            height: 40px;

            /* Orange */

            background: #F6934A;
            border-radius: 30px;

            /* Inside auto layout */

            flex: none;
            order: 2;
            flex-grow: 0;


            font-family: 'Open Sans';
            // font-style: normal;
            font-weight: 700;
            font-size: 17px;
            line-height: 50px;
            /* identical to box height, or 294% */

            // display: flex;
            // align-items: center;
            // text-align: center;

            color: $mh-white;

            /* Inside auto layout */
            // flex: none;
            // order: 0;
            // flex-grow: 0;
            cursor: pointer;
        };
    }
}

@media (min-width: 400px) {
    .login .login-title {
        margin: 0 35px 42px 35px ;
    }
}

